import { ReactNode, useState, useRef, useEffect } from 'react'
import classnames from 'classnames'
import { KeyValue } from '../../lib/keyboard'
import { CharStatus } from '../../lib/statuses'

type Props = {
  children?: ReactNode
  value: KeyValue
  width?: number
  status?: CharStatus
  onClick: (value: KeyValue) => void
  isEnter?: boolean
  isDelete?: boolean
  longPressOptions?: string[]  // Available options for long press
}

export const Key = ({
  children,
  status,
  width = 40,
  value,
  onClick,
  isEnter = false,
  isDelete = false,
  longPressOptions = [],
}: Props) => {
  const [showPopup, setShowPopup] = useState(false)
  const longPressTimerRef = useRef<NodeJS.Timeout | null>(null)
  const hideDelayTimerRef = useRef<NodeJS.Timeout | null>(null)
  const hasLongPress = longPressOptions.length > 0

  const classes = classnames(
    'flex items-center justify-center rounded mx-0.5 text-lg font-bold cursor-pointer select-none relative',
    {
      'bg-slate-200 hover:bg-slate-300 active:bg-slate-400': !status && !isEnter && !isDelete,
      'bg-slate-400 text-white': status === 'absent',
      'bg-red-500 hover:bg-red-600 active:bg-red-700 text-white':
        status === 'correct',
      'bg-yellow-400 hover:bg-yellow-600 active:bg-yellow-700 text-white':
        status === 'present',
      'bg-green-200 hover:bg-green-300 active:bg-green-400 text-gray-800': isEnter,
      'bg-red-200 hover:bg-red-300 active:bg-red-400 text-gray-800': isDelete,
    }
  )

  // Simple long press detection
  const startLongPress = () => {
    if (hasLongPress) {
      longPressTimerRef.current = setTimeout(() => {
        setShowPopup(true)
      }, 400) // 400ms long press
    }
  }

  const cancelLongPress = () => {
    if (longPressTimerRef.current) {
      clearTimeout(longPressTimerRef.current)
      longPressTimerRef.current = null
    }
  }

  const hidePopupWithDelay = () => {
    // Cancel any existing hide timer
    if (hideDelayTimerRef.current) {
      clearTimeout(hideDelayTimerRef.current)
    }
    
    // Set a delay before hiding the popup
    hideDelayTimerRef.current = setTimeout(() => {
      setShowPopup(false)
    }, 200) // 200ms delay to let user see the character
  }

  // Handle normal key clicks - types the base character (same as consonants)
  const handleClick = (event: React.MouseEvent) => {
    // Only trigger regular click if popup is not showing
    if (!showPopup) {
      onClick(value)
    }
  }

  // Handle macron option selection
  const handleMacronClick = (macronChar: string) => {
    console.log('Macron selected:', macronChar, 'hasLongPress:', hasLongPress, 'showPopup:', showPopup)
    onClick(macronChar)
    setShowPopup(false) // Hide popup immediately after selection
  }

  // Handle touch start - start timer for long press
  const handleTouchStart = (event: React.TouchEvent) => {
    if (hasLongPress) {
      startLongPress()
    }
  }

  // Handle touch end - cancel timer and hide popup with delay, or select macron
  const handleTouchEnd = (event: React.TouchEvent) => {
    if (hasLongPress) {
      cancelLongPress()
      if (showPopup) {
        // Check if finger is over a macron option when released
        const touch = event.changedTouches[0]
        if (touch) {
          const element = document.elementFromPoint(touch.clientX, touch.clientY)
          console.log('Touch end element:', element, 'tagName:', element?.tagName, 'textContent:', element?.textContent)
          if (element && element.tagName === 'BUTTON' && element.textContent && longPressOptions.includes(element.textContent)) {
            // Select the macron vowel
            console.log('Selecting macron via touch end:', element.textContent)
            handleMacronClick(element.textContent)
            return
          }
        }
        hidePopupWithDelay()
      }
    }
  }

  // Handle touch move for drag-to-select
  const handleTouchMove = (event: React.TouchEvent) => {
    if (hasLongPress && showPopup) {
      // Find which macron option is under the finger
      const touch = event.touches[0]
      if (touch) {
        const element = document.elementFromPoint(touch.clientX, touch.clientY)
        if (element && element.tagName === 'BUTTON' && element.textContent) {
          // Highlight the option being dragged over (optional visual feedback)
        }
      }
    }
  }

  // Handle mouse events - same as touch
  const handleMouseDown = () => {
    if (hasLongPress) {
      startLongPress()
    }
  }

  const handleMouseUp = () => {
    if (hasLongPress) {
      cancelLongPress()
      if (showPopup) {
        hidePopupWithDelay()
      }
    }
  }

  const handleMouseLeave = () => {
    if (hasLongPress) {
      cancelLongPress()
      setShowPopup(false) // Immediate hide when leaving the key
    }
  }

  // Cleanup timers on unmount
  useEffect(() => {
    return () => {
      if (longPressTimerRef.current) {
        clearTimeout(longPressTimerRef.current)
      }
      if (hideDelayTimerRef.current) {
        clearTimeout(hideDelayTimerRef.current)
      }
    }
  }, [])

  return (
    <div className="relative">
      <button
        style={{ width: `${width}px`, height: '58px' }}
        className={classes}
        onClick={handleClick}
        onMouseDown={hasLongPress ? handleMouseDown : undefined}
        onMouseUp={hasLongPress ? handleMouseUp : undefined}
        onMouseLeave={hasLongPress ? handleMouseLeave : undefined}
        onTouchStart={hasLongPress ? handleTouchStart : undefined}
        onTouchEnd={hasLongPress ? handleTouchEnd : undefined}
        onTouchMove={hasLongPress ? handleTouchMove : undefined}
      >
        {children || value}
      </button>
      
      {/* Clickable popup with macron options */}
      {showPopup && longPressOptions.length > 0 && (
        <div className="absolute bottom-full left-1/2 transform -translate-x-1/2 mb-2 bg-gray-800 text-white rounded-lg px-2 py-1 shadow-lg z-50">
          <div className="flex space-x-1">
            {longPressOptions.map((option) => (
              <button
                key={option}
                className="px-2 py-1 text-lg font-bold hover:bg-gray-600 rounded cursor-pointer"
                onClick={() => handleMacronClick(option)}
                onMouseDown={(e) => e.stopPropagation()} // Prevent triggering parent events
                onTouchStart={(e) => e.stopPropagation()} // Prevent triggering parent events
                onTouchEnd={(e) => {
                  e.stopPropagation()
                  handleMacronClick(option)
                }}
              >
                {option}
              </button>
            ))}
          </div>
          {/* Arrow pointing down */}
          <div className="absolute top-full left-1/2 transform -translate-x-1/2 w-0 h-0 border-l-4 border-r-4 border-t-4 border-transparent border-t-gray-800"></div>
        </div>
      )}
    </div>
  )
}
