import { KeyValue } from '../../lib/keyboard';
import { getStatuses } from '../../lib/statuses';
import { Key } from './Key';
import { useEffect } from 'react';
import { LanguageConfig } from '../../languages/frequency-types';
import { ScrollPrevention } from '../../utils/scrollPrevention';

type Props = {
  onChar: (value: string) => void
  onDelete: () => void
  onEnter: () => void
  guesses: string[][]
  orthography: string[]
  solution: string
  languageConfig?: LanguageConfig  // Optional - falls back to orthography-based layout
}

export const Keyboard = ({ onChar, onDelete, onEnter, guesses, orthography, solution, languageConfig }: Props) => {
  const charStatuses = getStatuses(guesses, solution, orthography)

  const onClick = (value: KeyValue) => {
    console.log('Keyboard onClick called with:', value)
    if (value === 'ENTER') {
      onEnter()
    } else if (value === 'DELETE') {
      onDelete()
    } else {
      console.log('Calling onChar with:', value.toLowerCase())
      onChar(value.toLowerCase())
    }
  }

  useEffect(() => {
    const listener = (e: KeyboardEvent) => {
      if (e.code === 'Enter') {
        onEnter()
      } else if (e.code === 'Backspace') {
        onDelete()
      }
    }
    window.addEventListener('keyup', listener)
    return () => {
      window.removeEventListener('keyup', listener)
    }
  }, [onEnter, onDelete, onChar])

  // Enable scroll prevention when keyboard mounts
  useEffect(() => {
    ScrollPrevention.enable()
    return () => {
      ScrollPrevention.disable()
    }
  }, [])

  // Use config-based layout if available, otherwise fall back to orthography-based layout
  if (languageConfig && 'keyboard' in languageConfig && languageConfig.keyboard) {
    const { rows, keyWidth = 40 } = languageConfig.keyboard;
    
    return (
      <div className="space-y-2" data-keyboard> {/* Added spacing between rows for popups */}
        {rows.map((row, rowIndex) => (
          <div key={rowIndex} className="flex justify-center">
            {row.map((char) => {
              if (char === 'DELETE') {
                return (
                  <Key key={char} value={char} onClick={onClick} isDelete width={keyWidth}>
                    <span className="font-bold"></span>
                  </Key>
                );
              } else if (char === 'ENTER') {
                return (
                  <Key key={char} value={char} onClick={onClick} isEnter width={keyWidth * 1.5}>
                    <span className="font-bold"></span>
                  </Key>
                );
              } else {
                // Check if this character has long press options
                const longPressOptions = languageConfig.keyboard.longPressMap[char.toUpperCase()] || [];
                
                return (
                  <Key 
                    key={char} 
                    value={char.toUpperCase()} 
                    onClick={onClick} 
                    status={charStatuses[char.toUpperCase()]} 
                    width={keyWidth}
                    longPressOptions={longPressOptions}
                  />
                );
              }
            })}
          </div>
        ))}
      </div>
    );
  }

  // Fallback: Original orthography-based layout
  const okinaChar = orthography.find(char => char === '' || char === "'")
  const nonOkinaChars = orthography.filter(char => char !== '' && char !== "'")
  
  const topRowEnd = 7 
  const middleRowEnd = topRowEnd + 6 

  return (
    <div data-keyboard>
      <div className="flex justify-center mb-1">
        {nonOkinaChars.slice(0, topRowEnd).map(
          (char) => (
            <Key key={char} value={char.toUpperCase()} onClick={onClick} status={charStatuses[char.toUpperCase()]} />
          )
        )}
        <Key value="DELETE" onClick={onClick} isDelete>
          <span className="font-bold"></span>
        </Key>
      </div>
      
      <div className="flex justify-center mb-1">
        {nonOkinaChars.slice(topRowEnd, middleRowEnd).map((char) => (
          <Key key={char} value={char.toUpperCase()} onClick={onClick} status={charStatuses[char.toUpperCase()]} />
        ))}
        {okinaChar && (
          <Key key={okinaChar} value={okinaChar.toUpperCase()} onClick={onClick} status={charStatuses[okinaChar.toUpperCase()]} />
        )}
      </div>
      
      <div className="flex justify-center">
        {nonOkinaChars.slice(middleRowEnd).map((char) => (
          <Key key={char} value={char.toUpperCase()} onClick={onClick} status={charStatuses[char.toUpperCase()]} />
        ))}
        <Key value="ENTER" onClick={onClick} isEnter>
          <span className="font-bold"></span>
        </Key>
      </div>
    </div>
  )
}