// Comprehensive scroll prevention system for mobile keyboards
// Prevents browser window movement during touch interactions

export class ScrollPrevention {
  private static isActive = false;
  private static originalStyles: { [key: string]: string } = {};
  private static touchStartY = 0;

  /**
   * Enable scroll prevention - called when keyboard becomes active
   */
  static enable() {
    if (this.isActive) return;
    
    this.isActive = true;
    
    // Store original styles for restoration
    this.originalStyles = {
      bodyOverflow: document.body.style.overflow,
      bodyPosition: document.body.style.position,
      bodyHeight: document.body.style.height,
      htmlOverflow: document.documentElement.style.overflow,
      bodyTouchAction: document.body.style.touchAction,
    };
    
    // Apply scroll prevention classes (more performant than inline styles)
    document.body.classList.add('scroll-prevention-active');
    document.documentElement.classList.add('scroll-prevention-active');
    
    // Add event listeners for touch prevention
    document.addEventListener('touchstart', this.handleTouchStart, { passive: false });
    document.addEventListener('touchmove', this.handleTouchMove, { passive: false });
    document.addEventListener('touchend', this.handleTouchEnd, { passive: false });
    
    // Prevent scroll on wheel events
    document.addEventListener('wheel', this.preventDefault, { passive: false });
    document.addEventListener('keydown', this.preventScrollKeys, { passive: false });
  }

  /**
   * Disable scroll prevention - called when keyboard becomes inactive
   */
  static disable() {
    if (!this.isActive) return;
    
    this.isActive = false;
    
    // Remove scroll prevention classes
    document.body.classList.remove('scroll-prevention-active');
    document.documentElement.classList.remove('scroll-prevention-active');
    
    // Remove event listeners
    document.removeEventListener('touchstart', this.handleTouchStart);
    document.removeEventListener('touchmove', this.handleTouchMove);
    document.removeEventListener('touchend', this.handleTouchEnd);
    document.removeEventListener('wheel', this.preventDefault);
    document.removeEventListener('keydown', this.preventScrollKeys);
  }

  private static handleTouchStart = (e: TouchEvent) => {
    // Record initial touch position
    if (e.touches.length === 1) {
      this.touchStartY = e.touches[0].clientY;
    }
  }

  private static handleTouchMove = (e: TouchEvent) => {
    // Only allow touch moves within keyboard area or very small movements
    const target = e.target as Element;
    const isKeyboardArea = target.closest('[data-keyboard]') || 
                          target.closest('.keyboard') ||
                          target.tagName === 'BUTTON';
    
    if (!isKeyboardArea) {
      // Prevent large scroll movements but allow small ones for precision
      if (e.touches.length === 1) {
        const currentY = e.touches[0].clientY;
        const deltaY = Math.abs(currentY - this.touchStartY);
        
        if (deltaY > 10) { // Threshold for scroll prevention
          e.preventDefault();
          return false;
        }
      }
    }
  }

  private static handleTouchEnd = (e: TouchEvent) => {
    // Reset touch tracking
    this.touchStartY = 0;
  }

  private static preventDefault = (e: Event) => {
    e.preventDefault();
    return false;
  }

  private static preventScrollKeys = (e: KeyboardEvent) => {
    // Prevent keyboard shortcuts that cause scrolling
    const scrollKeys = ['Space', 'PageUp', 'PageDown', 'End', 'Home', 'ArrowLeft', 'ArrowUp', 'ArrowRight', 'ArrowDown'];
    
    if (scrollKeys.includes(e.code)) {
      e.preventDefault();
      return false;
    }
  }

  /**
   * Check if scroll prevention is currently active
   */
  static get isEnabled(): boolean {
    return this.isActive;
  }
}